.board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }

  .square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 24px;
    line-height: 34px;
    height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 34px;
}

.square-bold{
    font-weight: bold;
}
  .square:focus {
    outline: none;
  }
  
.kbd-navigation .square:focus {
    background: #ddd;
  }

  .highlight{
      background: palegoldenrod;
  }


